<template lang="html">
	<div id='container'>

		<!--<tfHeader title="細胞存储预约" :show-goback="false" :show-center="true"></tfHeader>-->
		<div id='tittle'>
			<span>细胞存储预约</span>
		</div>
		<div id='msg_div'>
			<span>{{msg}}</span>
			<span>
				<a href="javascript:void(0);" @click="register($event);" v-show="isShow1">{{toRegister}}</a>
			</span>
			<div id='addBtn'>
				<span>
					<a href="javascript:void(0);" @click="addCellStorageAppoinment($event);"v-show='isShow2'>{{addAppointment}}</a>
				</span>
			</div>
		</div>
		<div id="appointContainer">
			<div  v-for="appointment in appointments">
				<router-link :to="{ name: 'wyCellStorageAppointmentDetail', params: {id : appointment.id} }" >
					<div class="content_show_div">
						<ul class="show_ul">
							<li class="show_li">
								<span class="show_span">存储项目：{{appointment.cellStorageProject}}</span>
							</li>
							<li class="show_li">
								<span class="show_span">同行人数：{{appointment.totalNumber}}</span>
							</li>
							<li class="show_li">
								<span class="show_span">同行人关系：{{appointment.relationShip}}</span>
							</li>
							<li class="show_li">
								<span class="show_span">有无未成年人同行：{{appointment.minor}}</span> 
							</li>
							<li class="show_li">
								<span class="show_span">参观日期：{{appointment.visitDate}}</span>
							</li>
							<li class="show_li">
								<span class="show_span">预约日期：{{appointment.appointmentDate}}</span>
								<span class="show_span"><a href="javascript:void(0);">{{viewAppointment}}</a></span>
							</li>
						</ul>
					</div>
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	import tfHeader from './tfHeader.vue'
	import wyCellStorageAppointmentAdd from './wyCellStorageAppointmentAdd.vue'
	import axios from 'axios'
//	import $ from 'jquery'
	import dateUtils from '../utils/dateUtils'
	import domainUtils from '../utils/domain'
	
	export default {
		data() {
			return {
				wxCode: '',
				wxOpenId: '',
				mobile: '',
				msg: "",
				addAppointment: "添加预约",
				viewAppointment: "查看详情",
				isShow1: false,
				isShow2: true,
				appointments: [] 
			}
		},
		methods: {
			isLocalStorage: function(storage) {
				if(!!storage) {
					try {
						storage.setItem("key", "value");
						storage.removeItem("key");
						return true;
					} catch(e) {
						return false;
					}
				} else {
					return false;
				}
			},
			register: function(event) {
				this.$router.push({
					name: 'wyRegister',
					params: {
						wxOpenId: window.localStorage.getItem("wxOpenId"),
						retComponent:'1'
					}
				})
			},
			login() {
				var message = this.$route.params.msg;
				var data = this.$route.query.data;

				if(message == "deleteFailed") {
					this.msg = "取消预约失败！";
				}

				if(message == "deleteSuccess") {
					this.msg = "取消预约成功！";
				}

				if(message == "registerFailed") {
					this.isShow2 = false;
					this.isShow1 = true;
				}
				if(message == "infoAddSuccess") {
					this.msg = "个人信息完善成功！"
				}
				if(!!data) {
					if(data == "getOpenidError" || data == "weixinauthError" ) {
						this.msg = "微信授权出错"
					} else {
						var data1 = JSON.parse(data);
						var wxOpenId = data1.wxOpenId;

						if(this.$options.methods.isLocalStorage(window.localStorage)) {
							window.localStorage.setItem("wxOpenId", wxOpenId);
						} else {
							alert("请关闭无痕浏览")
						}

						if(data1.isRegistered) {
							if(!!data1.appointments) {
								data1.appointments.forEach((value, index) => {
									for(var key in value) {
										if(key == 'appointmentDate') {
											value.appointmentDate = dateUtils.getDate(value.appointmentDate);
										}
										if(key == 'visitDate') {
											value.visitDate = dateUtils.getDate(value.visitDate)
										}
									}
									this.appointments.push(value);
								});
							} else {
								this.msg = '没有预约记录'
							}
						} else {
							this.$router.push({
								name: 'wyRegister',
								params: {
									wxOpenId: wxOpenId,
									retComponent:'1'
								}
							})
						}

					}
				} else {
					//vue 頁面跳轉過來
					let wxOpenId = window.localStorage.getItem('wxOpenId');
					//不是注册失败页面跳转到本页面。注册失败时无需请求
					if(!this.isShow1) {
						if(!!wxOpenId) {
							var url = domainUtils.domain +"/loadCellStorageAppointmentList";

							this.$post(url, {
								wxOpenId: wxOpenId
							}).then((data) => {
								if(data.status == "success") {
									if(!!data.data) {
										data.data.forEach((value, index) => {
											for(var key in value) {
												if(key == 'appointmentDate') {
													value.appointmentDate = dateUtils.getDate(value.appointmentDate);
												}
												if(key == 'visitDate') {
													value.visitDate = dateUtils.getDate(value.visitDate)
												}
											}
											this.appointments.push(value);
										});
									} else {
										this.msg = data.remark;
									}
								} else {
									this.msg = data.remark;
								}
							})
						} else {
							var REDIRECT_URI = domainUtils.domain +"/redirectToCellStorageCenter";
							var APPID = "wx5753bd84103aaada";
							var SCOPE = 'snsapi_base';
							var STATE = "123";
							var url = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" + APPID + "&redirect_uri=" + REDIRECT_URI + "&response_type=code&scope=" + SCOPE + "&state=" + STATE + "#wechat_redirect";
							window.location.href = url;
						}
					}
				}

			},
			addCellStorageAppoinment: function(envent) {
				this.$router.push({
					name: 'wyCellStorageAppointmentAdd',
				})
			}
		},
		created() {
			this.login()
		},
		components: {
			tfHeader,
			wyCellStorageAppointmentAdd
		}
	}
</script>

<style lang="scss">
	@import "../assets/scss/common.scss";
	@import "../assets/scss/wyCenter.scss";
</style>


